@use "global" as *;


.about {
  padding-top: rem(96);
  padding-bottom: rem(106);
  background: $color-background-base-1;
  position: relative;

  min-height: rem(754);

  @include mq(md) {
    padding-top: rem(250);
    padding-bottom: rem(200);
  }
}

.about__img-left {
  display: block;
  width: fit-content;
  position: absolute;
  bottom: 0;
  // left: 0;
  left: calc(50% - min(50vw, 45rem));  // *位置を制御

  border-radius: 0px rem(119) 0px 0px;
  overflow: hidden;

  @include mq(md) {
    border-radius: 0px rem(119) 0px 0px;
  }
}

.about__img-left img {
  aspect-ratio: 180 / 200.875;
  // width: rem(180);
  width: calc(180px / 1440px * 100vw);
  max-width: rem(180);
  object-fit: cover;

  @include mq(md) {
    aspect-ratio: 196.5 / 136;
    width: rem(196.5);
  }
}

.about__img-right {
  display: block;
  width: fit-content;
  position: absolute;
  top: 0;
  // right: 0;
  right: calc(50% - min(50vw, 45rem));  // *位置を制御

  border-radius: 0px 0px 0px rem(305.5);
  overflow: hidden;

  @include mq(md) {
    border-radius: 0px 0px 0px rem(160);
  }
}

.about__img-right img {
  aspect-ratio: 628.9 / 661.36;
  // width: rem(628.9);
  width: calc(628.9px / 1440px * 100vw);
  max-width: rem(628.9);
  object-fit: cover;

  @include mq(md) {
    aspect-ratio: 262 / 192;
    width: rem(262);
  }
}

.about__inner {
  @include mq(md) {
    max-width: rem(calc(550 + 24.5 * 2));  // 両端のpadding分を考慮
    margin-inline: auto;
  }
}

.about__wrap {
  width: fit-content;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  // margin-left: rem(-10);  // 位置調整
  position: absolute;
  top: rem(96);
  left: calc(50% - min(42vw, 33.75rem));  // *位置を制御

  @include mq(md) {
    position: static;
    top: auto;
    left: auto;
    margin-inline: auto;
  }
}

.about__title {

}

.about__text01 {
  margin-top: rem(30);
  text-align: center;
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-ja-1;
  font-size: rem(32);
  font-weight: $bold;
  line-height: 1.5;

  @include mq(md) {
    margin-top: rem(25);
    font-size: rem(20);
  }
}

.about__text02 {
  margin-top: rem(30);
  text-align: center;
  leading-trim: both;
  text-edge: cap;
  font-size: rem(16);
  line-height: 2.0;

  @include mq(md) {
    margin-top: rem(25);
    font-size: rem(14);
  }
}

.about__btn-wrap {

}

.about__btn {
  margin-top: rem(40);
  min-width: rem(251);

  @include mq(md) {
    margin-top: rem(35);
    min-width: rem(240);
  }
}
